<template lang="pug">
div
  button(class="btn btn-primary" @click="fullscreen") 全螢幕
</template>

<script>
import screenfull from 'screenfull'
export default {
  data () {
    return {
      isFullscreen: false
    }
  },
  methods: {
    fullscreen () {
      if (!screenfull.enabled) {
        this.$message({
          message: '不支持全螢幕',
          type: 'warning'
        })
        return false
      }

      screenfull.toggle()
      this.$message({
        message: '全螢幕',
        type: 'success'
      })
    }
  }
}
</script>



