<template lang="pug">
  div(class="m-4 mb-5")
    div.gridContainer
      .type 行數
        span Row
      input#gridRow(
        type="number" min="1" max="16"
        class="form-control form-control-sm"
        :value="grid.row" @input="grid.row = parseInt($event.target.value)"
      )
      span.spinner
        i(class="fas fa-sort-up" @click="addRow(16)").d-inline-block
        i(class="fas fa-sort-down" @click="subRow(1)").d-inline-block
    div.gridContainer
      .type 欄數
        span Column
      input#gridHeight(
        type="number" min="1" max="18"
        class="form-control form-control-sm"
        :value="grid.column" @input="grid.column = parseInt($event.target.value)"
      )
      span.spinner
        i(class="fas fa-sort-up" @click="addColumn(18)").d-inline-block
        i(class="fas fa-sort-down" @click="subColumn(1)").d-inline-block
    div.gridContainer
      .type 欄間距
        span Gutter
      input#gridGutter(
        type="number" min="0" max="35"
        class="form-control form-control-sm"
        :value="grid.gutter" @input="grid.gutter = parseInt($event.target.value)"
      )
      span.spinner
        i(class="fas fa-sort-up" @click="addGutter(35)").d-inline-block
        i(class="fas fa-sort-down" @click="subGutter(0)").d-inline-block
    div.gridContainer
      .type 格線
        span Line
      label(class="switch")
        input#gridLine(
          type="checkbox"
          v-model="grid.isGridLine"
        )
        span
    div.gridContainer(class="border-0")
      .type 索引
        span Index
      label(class="switch")
        input#gridIndex(
          type="checkbox"
          v-model="grid.isGridIndex"
        )
        span
</template>

<script>
export default {
  data () {
    return {
      grid: {
        row: 0,
        column: 0,
        gutter: 0,
        blocks: 0,
        liWidth: 0,
        liHeight: 0,
        isGridLine: true,
        isGridIndex: true
      }
    }
  },
  methods: {
    addRow (max) {
      if (this.grid.row < max) {
        this.grid.row++
      }
    },
    addColumn (max) {
      if (this.grid.column < max) {
        this.grid.column++
      }
    },
    addGutter (max) {
      if (this.grid.gutter < max) {
        this.grid.gutter++
      }
    },
    subRow (min) {
      if (this.grid.row > min) {
        this.grid.row--
      }
    },
    subColumn (min) {
      if (this.grid.column > min) {
        this.grid.column--
      }
    },
    subGutter (min) {
      if (this.grid.gutter > min) {
        this.grid.gutter--
      }
    }
  },
  computed: {
    getBlocks () {
      return this.grid.row * this.grid.column
    },
    getliWidth () {
      return (100 / this.grid.column) + '%'
    },
    getliHeight () {
      return (100 / this.grid.row) + '%'
    }
  },
  created () {
    this.grid.liWidth = this.getliWidth
    this.grid.liHeight = this.getliHeight
    this.grid.blocks = this.getBlocks

    if (this.$mq === 'lg') {
      this.grid.row = 6
      this.grid.column = 12
      this.grid.gutter = 15
    } else {
      this.grid.row = 4
      this.grid.column = 6
      this.grid.gutter = 10
    }

    setTimeout(() => {
      this.$bus.$emit('initGrid', this.grid)
    }, 10)
  }
}
</script>

<style scope lang="scss">
</style>



