<template lang="pug">
.div(class="m-4 mb-5 border rounded")
  ul#colorsheet(class="px-3 py-2 m-0")
    li(v-for='c in colorsheet') ${{ c.colorName.trim() }}: {{ c.colorCode.toUpperCase() }};
  .copyBtn(class="text-center py-2" @click="copyAll")
    i(class="fas fa-clipboard mr-2")
    | COPY ALL
</template>

<script>
import $ from 'jquery'

export default {
  data () {
    return {
      colorsheet: []
    }
  },
  methods: {
    strCopy (v) {
      let $temp = $('<textarea>')
      $('body').append($temp)
      $temp.val(v).select()
      document.execCommand('Copy')
      $temp.remove()
    },
    copyAll () {
      this.strCopy(this.getColorsheet)
    }
  },
  computed: {
    getColorsheet () {
      let result = []
      this.colorsheet.forEach(
        (c) => {
          result.push(`$${c.colorName}: ${c.colorCode};`)
        }
      )
      return result.join('\n')
    }
  },
  created () {
    const vm = this
    vm.$bus.$on('initColorsheet', (value) => {
      vm.colorsheet = value
    })
  }
}
</script>



