<template lang="pug">
div.container(class="pt-md-5 pt-lg-5 pt-4 d-flex flex-column align-items-center")
  div#screen
    ul#grid(
      ref="grid"
      :class="{'fixed':isFull, 'gridLine':grid.isGridLine}"
      :style="ulStyle"
    )
      li(
        v-for="(c,index) in grid.blocks"
        :style="liStyle"
      )
        span(:class="{'d-none':!grid.isGridIndex}") {{ index+1 }}
        div.content

  div#control(class="d-lg-none d-flex justify-content-around mt-md-4 mt-3")
    div(class="d-flex flex-column mr-3")
      div.gridContainer
        .type 行數
          span Row
        input#gridRow(
          type="number" min="1" max="16"
          class="form-control form-control-sm"
          :value="grid.row" @input="grid.row = parseInt($event.target.value)"
        )
        span.spinner
          i(class="fas fa-sort-up" @click="addRow(16)").d-inline-block
          i(class="fas fa-sort-down" @click="subRow(1)").d-inline-block
      div.gridContainer
        .type 欄數
          span Column
        input#gridHeight(
          type="number" min="1" max="18"
          class="form-control form-control-sm"
          :value="grid.column" @input="grid.column = parseInt($event.target.value)"
        )
        span.spinner
          i(class="fas fa-sort-up" @click="addColumn(18)").d-inline-block
          i(class="fas fa-sort-down" @click="subColumn(1)").d-inline-block
      div.gridContainer(class="border-bottom-0")
        .type 欄間距
          span Gutter
        input#gridGutter(
          type="number" min="0" max="35"
          class="form-control form-control-sm"
          :value="grid.gutter" @input="grid.gutter = parseInt($event.target.value)"
        )
        span.spinner
          i(class="fas fa-sort-up" @click="addGutter(35)").d-inline-block
          i(class="fas fa-sort-down" @click="subGutter(0)").d-inline-block
    div(class="d-flex flex-column")
      div.gridContainer
        .type 格線
          span Line
        label(class="switch")
          input#gridLine(
            type="checkbox"
            v-model="grid.isGridLine"
          )
          span
      div.gridContainer(class="border-0")
        .type 索引
          span Index
        label(class="switch")
          input#gridIndex(
            type="checkbox"
            v-model="grid.isGridIndex"
          )
          span

  .toolBtns
    .fullBtn(@click="isFull = !isFull")
      i(class="fas fa-external-link-alt flip-h")
</template>

<script>
export default {
  data () {
    return {
      isFull: false,
      ratio: 0, // 裝置螢幕比
      minWidth: 0, // 裝置最小寬度
      preview: 0, // 預覽時縮減大小比
      grid: {},
      // 預覽狀態下的網格寬、高、gutter
      gridWidth: 0,
      gridHeight: 0,
      displayGridGutter: 0
    }
  },
  watch: {
    // 如果欄列數量有更改時重新計算格數(blocks)、行寬(liWidth)、行高(liHeight)
    'grid.row' (newRow) {
      if ((newRow > 0) && (newRow <= 16)) {
        this.grid.blocks = this.getBlocks
        this.grid.liHeight = this.getliHeight
      }
    },
    'grid.column' (newColumn) {
      if ((newColumn > 0) && (newColumn <= 18)) {
        this.grid.blocks = this.getBlocks
        this.grid.liWidth = this.getliWidth
      }
    },
    'grid.blocks' (newBlocks) {
      this.grid.blocks = newBlocks
    },
    'grid.liWidth' (newLiWidth) {
      this.grid.liWidth = newLiWidth
    },
    'grid.liHeight' (newLiHeight) {
      this.grid.liHeight = newLiHeight
    }
  },
  methods: {
    addRow (max) {
      if (this.grid.row < max) {
        this.grid.row++
      }
    },
    addColumn (max) {
      if (this.grid.column < max) {
        this.grid.column++
      }
    },
    addGutter (max) {
      if (this.grid.gutter < max) {
        this.grid.gutter++
      }
    },
    subRow (min) {
      if (this.grid.row > min) {
        this.grid.row--
      }
    },
    subColumn (min) {
      if (this.grid.column > min) {
        this.grid.column--
      }
    },
    subGutter (min) {
      if (this.grid.gutter > min) {
        this.grid.gutter--
      }
    }
  },
  computed: {
    preveiwSize () {
      if (this.$mq === 'lg') {
        return 1.5
      } else if (this.$mq === 'md') {
        return 1.6
      } else {
        return 1.6
      }
    },
    deviceMinWidth () {
      if (this.$mq === 'lg') {
        return 992
      } else if (this.$mq === 'md') {
        return 400
      } else {
        return 100
      }
    },
    deviceRatio () {
      if (this.$mq === 'lg') { // desktops
        return (window.screen.availWidth / document.documentElement.clientHeight)
      } else if (this.$mq === 'md') {
        return (3 / 4) // tablets
      } else if (this.$mq === 'sm') {
        return (16 / 9) // landscape phones
      } else {
        return (9 / 16) // portrait phones
      }
    },
    getBlocks () {
      return this.grid.row * this.grid.column
    },
    getliWidth () {
      return (100 / this.grid.column) + '%'
    },
    getliHeight () {
      return (100 / this.grid.row) + '%'
    },
    getGutter () {
      // min-gutter
      if (this.gridWidth * 1.5 < this.minWidth) {
        return this.grid.gutter * ((this.minWidth / 1.5) / screen.width)
      } else {
        return this.grid.gutter * (this.gridWidth / screen.width)
      }
    },
    ulStyle () {
      if (this.isFull) { // 全螢幕
        return {
          width: screen.width + 'px',
          height: document.documentElement.clientHeight + 'px'
        }
      } else { // 預覽模式
        return {
          minWidth: (this.minWidth / 1.5) + 'px',
          minHeight: (this.minWidth / 1.5 / this.ratio) + 'px',
          width: this.gridWidth + 'px',
          height: this.gridHeight + 'px'
        }
      }
    },
    liStyle () {
      if (this.isFull) { // 全螢幕
        return {
          flex: this.grid.liWidth,
          height: this.grid.liHeight,
          paddingLeft: this.grid.gutter + 'px',
          paddingRight: this.grid.gutter + 'px'
        }
      } else {
        return { // 預覽模式
          flex: this.grid.liWidth,
          height: this.grid.liHeight,
          paddingLeft: this.getGutter + 'px',
          paddingRight: this.getGutter + 'px'
        }
      }
    }
  },
  mounted () {
    const vm = this
    vm.$bus.$on('initGrid', (value) => {
      vm.grid = value
    })

    this.minWidth = this.deviceMinWidth

    // 初始預覽網格
    this.ratio = (window.screen.availWidth / document.documentElement.clientHeight)
    this.preview = this.preveiwSize
    this.gridWidth = window.innerWidth / 1.5
    this.gridHeight = window.innerWidth / 1.5 / this.ratio
    this.displayGridGutter = this.getGutter

    // 視窗大小改變時重新計算寬高
    window.onresize = () => {
      return (() => {
        this.gridWidth = window.innerWidth / 1.5
        this.gridHeight = window.innerWidth / 1.5 / this.ratio
        this.displayGridGutter = this.getGutter
        this.minWidth = this.deviceMinWidth
      })()
    }
  }
}
</script>



