<template lang="pug">
div.container(class="p-5")
  ul#colorPalette
    li(v-for="(c, key) in colorsheet" :style="{backgroundColor: c.colorCode}")
  hr
  .row
    div(class="col-sm-6 col-md-4 col-lg-3 p-3" v-for="(c, key) in colorsheet")
      .removeArea
        .removeBtn(@click.prevent="removeColor(key)")
          i(class="fas fa-times")
      .color-card.rounded(:id="c.id" class="animated" :class="{'bounceIn':c.show}")
        .color-block(:style="{backgroundColor: c.colorCode}" @click.prevent="copyColorCode(c)")
        .color-info(class="bg-white px-2 py-2")
          .color-name(
            @dblclick.prevent="editColor(c, 'name')"
            v-if="(c.id !== tempColor.id) || (c.isEdit === 'code')"
          )
            |{{ c.colorName.toUpperCase() }}
          input(
            type="text"
            class="form-control" :class="validColorNameStyle(c.colorName)"
            @keyup.esc="cancelEdit(c)"
            @keyup.enter="doneEdit(c)"
            @blur="doneEdit(c)"
            v-focus="true"
            :value="c.colorName" @input="validInput(c, $event.target.value)"
            v-if="(c.id === tempColor.id) && (c.isEdit === 'name')"
          )
          .color-code(
            @dblclick.prevent="editColor(c, 'code')"
            v-if="(c.id !== tempColor.id) || (c.isEdit === 'name')"
          )
            |{{ c.colorCode.toUpperCase() }}
          input(
            type="text"
            class="form-control" :class="validColorHexStyle(c.colorCode)"
            @keyup.esc="cancelEdit(c)"
            @keyup.enter="doneEdit(c)"
            @blur="doneEdit(c)"
            v-focus="true"
            :value="c.colorCode" @input="validInput(c, $event.target.value)"
            v-if="(c.id === tempColor.id) && (c.isEdit === 'code')"
          )
    .toolBtns
      div(class="addContainer" tabindex="1")
        input(
          type="text" placeholder="變數名稱"
          class="form-control mr-3" :class="validColorNameStyle(newColorName)" v-model="newColorName"
          @keyup.enter.prevent="addColor()"
        )
        input(
          type="text" placeholder="#DDC9F2"
          class="form-control" :class="validColorHexStyle(newColorCode)" v-model="newColorCode"
          @keyup.enter.prevent="addColor()"
        )
        .addBtn(@click.prevent="addColor()")
          i(class="fas fa-plus")
</template>

<script>
import $ from 'jquery'

export default {
  name: 'SassColorGuide',
  directives: {
    focus: {
      inserted: function (el) {
        el.focus()
      }
    }
  },
  data () {
    return {
      colorinit: `
        $color_bg: #E0CAD0;
        $color_pink: #CE7597;
        $color_pinkL: #FDBBC9;
        $color_blue: #BBDDEE;
        $color_md_1: #F3D1BA;
        $color_md_2: #F7E1CD;
        `,
      colorsheet: [],
      tempColor: {},
      newColorName: '',
      newColorCode: ''
    }
  },
  methods: {

    // -------------------------------------
    //                驗證資料
    // -------------------------------------

    validColorName (cName) {
      return cName.trim() !== ''
    },
    validColorNameStyle (cName) {
      return (cName === '') ? '' : this.validColorName(cName) ? '' : 'is-invalid'
    },
    validColorHex (cCode) {
      let hexRegex = new RegExp('^#.[0-9a-f]+$', 'i')
      return hexRegex.test(cCode)
    },
    validColorHexStyle (cCode) {
      return (cCode === '') ? '' : this.validColorHex(cCode) ? '' : 'is-invalid'
    },

    // -------------------------------------
    //            複製色碼與程式碼
    // -------------------------------------

    strCopy (v) {
      let $temp = $('<textarea>')
      $('body').append($temp)
      $temp.val(v).select()
      document.execCommand('Copy')
      $temp.remove()
    },
    copyColorCode (c) {
      // Copy Color Code
      this.strCopy(c.colorCode)

      // // Text Animation
      let copyColor = $(`#${c.id} .color-block`)
      copyColor.append('<div class="colorActive animated fadeOut">')
      setTimeout(() => copyColor.empty(), 1000)
    },

    // -------------------------------------
    //                新增卡片
    // -------------------------------------

    addColor () {
      // 如果驗證錯誤就不能觸發按鈕
      if (!this.validColorName(this.newColorName) || !this.validColorHex) {
        return
      }
      this.addToColorsheet()

      // 移除動畫.bounceIn
      let last = this.colorsheet[this.colorsheet.length - 1]
      setTimeout(() => (last.show = false), 1000)
    },
    addToColorsheet () {
      let timestamp = Math.floor(Date.now())
      this.colorsheet.push({
        show: true,
        id: timestamp,
        colorName: this.newColorName,
        colorCode: this.newColorCode
      })

      this.newColorName = ''
      this.newColorCode = ''
    },

    // -------------------------------------
    //              編輯卡片資料
    // -------------------------------------

    removeColor (key) {
      this.colorsheet.splice(key, 1)
    },
    editColor (c, type) {
      c.isEdit = type
      this.tempColor = Object.assign({}, c)
    },
    cancelEdit (c) {
      if (c.isEdit === 'name') {
        c.colorName = this.tempColor.colorName
      } else if (c.isEdit === 'code') {
        c.colorCode = this.tempColor.colorCode
      }
      this.tempColor = {}
    },
    doneEdit (c) {
      if (!this.validColorName(c.colorName)) { // 名稱驗證錯誤
        c.colorName = this.tempColor.colorName
      } else if (!this.validColorHex(c.colorCode)) { // 色碼驗證錯誤
        c.colorCode = this.tempColor.colorCode
      }
      this.tempColor = {}
    },
    validInput (c, val) {
      if (c.isEdit === 'name') {
        // input空白時顯示.is-invalid（改為多一個空白' '）
        (val === '') ? c.colorName = ' ' : c.colorName = val
      } else if (c.isEdit === 'code') {
        (val === '') ? c.colorCode = ' ' : c.colorCode = val
      }
    }
  },
  computed: {
    colorInit () {
      let result = []
      this.colorinit.split('\n').forEach(
        (obj, index) => {
          if (obj.indexOf('color') !== -1) {
            result.push({
              show: false,
              id: index,
              colorName: obj.split(':')[0].split('$')[1],
              colorCode: obj.split(':')[1].trim().split(';')[0],
              isEdit: ''
            })
          }
        }
      )
      return result
    }
  },
  created () {
    this.colorsheet = this.colorInit
  },
  mounted () {
    this.$bus.$emit('initColorsheet', this.colorsheet)
  }
}
</script>

<style scoped lang="scss">
</style>



