import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '@/components/Home'
import WebTools from '@/components/WebTools'
import SassColorGuide from '@/components/Pages/WebTools/SassColorGuide'
import GridSystem from '@/components/Pages/WebTools/GridSystem'

Vue.use(VueRouter)

export default new VueRouter({
  linkActiveClass: 'is-active',
  routes: [
    {
      name: 'Home',
      path: '/',
      component: Home
    },
    {
      name: 'WebTools',
      path: '/webtools',
      component: WebTools,
      children: [
        {
          name: 'SassColorGuide',
          path: 'color-guide',
          component: SassColorGuide
        },
        {
          name: 'GridSystem',
          path: 'grid-system',
          component: GridSystem
        }
      ]
    }
  ]
})



