<template lang="pug">
  div#sidebar(class="col col-lg-3 p-0")
    .top(class="position-relative" :style="{backgroundColor: title.bgColor}")
      .title(class="pl-5 pl-lg-4 pb-lg-3")
        h5(v-html="title.subText")
        h1.m-0(v-html="title.titleText")
    .bottom(class="d-none d-lg-flex flex-column bg-white")
      keep-alive
        SidebarSass(v-if="this.currentPage === 'SassColorGuide'")
        SidebarGrid(v-if="this.currentPage === 'GridSystem'")
</template>

<script>
import SidebarSass from './Sidebar_sass'
import SidebarGrid from './Sidebar_grid'

export default {
  components: {
    SidebarSass,
    SidebarGrid
  },
  props: ['newTitle', 'currentPage'],
  data () {
    return {
      title: this.newTitle
    }
  },
  watch: {
    'newTitle' (newVal) {
      this.title = newVal
    }
  },
  created () {
  }
}
</script>

<style scoped lang="scss">

</style>



