<template lang="pug">
div#webtools
  Navbar(:nav-item-color="Page.bgColor" v-if="Page.bgColor")
  .container-fluid
    .row
      //根據頁面插入對應標題(title)
      Sidebar(:new-title="Page" :current-page="currentPage" v-if="Page.titleText")
      div(class="col offset-lg-3 offset-0 bg-light")
        keep-alive
          router-view
</template>

<script>
import Navbar from './Navbar'
import Sidebar from './Sidebar/Sidebar'

export default {
  components: {
    Navbar,
    Sidebar
  },
  data () {
    return {
      currentPage: '',
      ifScroll: 'scroll',
      Title: {
        SassColorGuide: {
          titleText: 'SASS 色卡工具',
          subText: 'COLOR GUIDE<br>HTML COLOR CODES',
          bgColor: '#FDBBC9'
        },
        GridSystem: {
          titleText: '網格工具',
          subText: 'GRID SYSTEM<br>CUSTOMIZING THE GRID',
          bgColor: '#BBDDEE'
        }
      }
    }
  },
  watch: {
    '$route' (to, from) {
      this.currentPage = to.name
    }
  },
  computed: {
    Page () {
      if (this.currentPage === 'SassColorGuide') {
        return this.Title.SassColorGuide
      } else if (this.currentPage === 'GridSystem') {
        return this.Title.GridSystem
      }
    }
  },
  created () {
    this.currentPage = this.$route.name
  }
}
</script>

<style scope lang="scss">
.container {
  min-height: calc(100vh - 4rem);
}
</style>



